import { injectGlobal } from 'styled-components';
import theme from './theme';
import mixins from './mixins';
import media from './media';
import * as fonts from './fonts';

const base = injectGlobal`
 
  @font-face {
    font-family: 'SFProText';
    src: url(${fonts.SFTextOTF}) format('woff2'),
    url(${fonts.SFTextWOFF}) format('woff'),
    url(${fonts.SFTextWOFF2}) format('openfont');
    font-weight: 400;
    font-style: normal;
    font-display: auto;
  }
  @font-face {
    font-family: 'SFProText';
    src: url(${fonts.SFTextMidOTF}) format('woff2'),
    url(${fonts.SFTextMidWOFF}) format('woff'),
    url(${fonts.SFTextMidWOFF2}) format('openfont');
    font-weight: 400;
    font-style: normal;
    font-display: auto;
  }

  @font-face {
    font-family: 'RBNo31L';
    src: url(${fonts.RBNo31LightWOFF2}) format('woff2'),
    url(${fonts.RBNo31LightWOFF}) format('woff'),
    url(${fonts.RBNo31LightOTF}) format('openfont');
    font-weight: 300;
    font-style: normal;
    font-display: auto;
  }

  html {
    box-sizing: border-box;
    width: 100%;
  }

  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }

  body {
    margin: 0;
    width: 100%;
    min-height: 100%;
    overflow-x: hidden;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    background-color: ${theme.colors.white};
    color: ${theme.colors.darkblueLight};
    line-height: 1.25;
    
    font-family: ${theme.fonts.SFProText};
    font-size: ${theme.fontSizes.xlarge};
    ${media.phablet`font-size: ${theme.fontSizes.large};`}

    &.hidden {
      overflow: hidden;
    }
    &.blur {
      overflow: hidden;
      #root > .container > * {
        filter: blur(5px) brightness(0.7);
        transition: ${theme.transition};
        pointer-events: none;
        user-select: none;
      }
    }
  }

  ::selection {
    background-color: ${theme.colors.yellow} ;
    color:  ${theme.colors.blueGrey};
    
  }

  h1,
  h3,
  h4,
  h5 {
    font-weight: 600;
    color: #000;
    margin: 0 0 10px 0;
    font-family: ${theme.fonts.SFProText};
  }

  h2 {
    font-family: ${theme.fonts.RBNo31};
    color: ${theme.colors.darkblue};
  }

  #root {
    min-height: 100vh;
    display: grid;
    grid-template-rows: 1fr auto;
    grid-template-columns: 100%;
  }

  a {
    ${mixins.link};
  }

  svg {
    width: 100%;
    height: 100%;
    fill: currentColor;
    vertical-align: middle;
  }

  button {
    outline: 0;
    border: 0;
  }

  p {
    margin-top: 0;
    font-family: ${theme.fonts.Calibre};
  }

  .gatsby-image-outer-wrapper {
    height: 100%;
  }

  .fadeup-enter {
    opacity: 0.01;
    transform: translateY(20px);
    transition: opacity 300ms ${theme.easing}, transform 300ms ${theme.easing};
  }

  .fadeup-enter-active {
    opacity: 1;
    transform: translateY(0px);
    transition: opacity 300ms ${theme.easing}, transform 300ms ${theme.easing};
  }

  .fadedown-enter {
    opacity: 0.01;
    transform: translateY(-20px);
    transition: opacity 300ms ${theme.easing}, transform 300ms ${theme.easing};
  }

  .fadedown-enter-active {
    opacity: 1;
    transform: translateY(0px);
    transition: opacity 300ms ${theme.easing}, transform 300ms ${theme.easing};
  }

  .fade-enter {
    opacity: 0.01;
    transition: opacity 1000ms ${theme.easing};
  }

  .fade-enter-active {
    opacity: 1;
    transition: opacity 1000ms ${theme.easing};
  }

`;

export default base;
